body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  color: #3a3a3a;
  font-family: 'Brandon Grotesque','brandon-grotesque-n7','brandon-grotesque',Helvetica,sans-serif;
  font-size: 14px;
}

table {
  border-collapse: collapse;
  table-layout: fixed;
  width: 100%;
}

td,
th { 
	overflow: hidden;
	text-align: left;
	vertical-align: middle;
}

th a,
td a { 
	display: block;
	width: 100%;
}

th a:link,
th a:visited,
th a:hover,
th a:active {
	color: #000;
	text-decoration: none;
}

a.sort-by { 
  cursor: pointer;
	padding-right: 18px;
	position: relative;
}

a.sort-by:before,
a.sort-by:after {
	border: 4px solid transparent;
	content: "";
	display: block;
	height: 0;
	right: 5px;
	top: 50%;
	position: absolute;
	width: 0;
}

a.sort-by:before {
	border-bottom-color: #666;
	margin-top: -9px;
}

a.sort-by:after {
	border-top-color: #666;
	margin-top: 1px;
}

td, th {
  border-bottom: 1px solid rgba(224, 224, 224, 1);
  overflow: hidden;
  padding: 16px;
  text-overflow: ellipsis;
  white-space: nowrap;
}

th.row-details {
  width: 60%;
}

th.row-flight-number {
  width: 160px;
}

tr {
  height: 50px;
  border-collapse: collapse;
  border-spacing: 0;
}

tr.clickable-row {
  cursor: pointer;
}

tr.clickable-row:hover {
  background-color: #3a3a3a;
  color: #fff;
}

tr.clickable-row:hover td {
  white-space: normal
}

tfoot .pagination {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end
}

button {
  background: #000;
  border-radius: 0;
  border: 0;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-weight: bold;
  margin: 0 16px;
  padding: 1em 2.182em;
  text-transform: uppercase;
}

button:disabled {
  cursor: not-allowed;
  opacity: 0.5;
}

